.ZStackScroller {
    position: relative;
    width: 600px; 
    height: 600px;
    text-align: center;
}
  
.ZStackScroller-imagecontainer {
    position:absolute;
    top:0px; 
    left:0px; 
    width:560px; 
    height:560px;
}

.ZStackScroller-btn {
    position: absolute;
    background-color: DodgerBlue; /* Blue background */
    border: none; /* Remove borders */
    color: white; /* White text */
    /* padding: 12px 16px; /* Some padding */
    font-size: 16px; /* Set a font size */
    cursor: pointer; /* Mouse pointer on hover */

    transform: rotate(-90deg);
}

.ZStackScroller-playbtn {
    top:100px; 
    left:585px;
}
.ZStackScroller-nextbtn {
    top:0px; 
    left:560px;
}
.ZStackScroller-prevbtn {
    top:224px; 
    left:560px;
}

.ZStackScroller-img {
    object-fit: "contain";
    height: 100%;
    width: 100%;
}

.ZStackScroller-slider {
    position:absolute; 
    top:20px; 
    left:560px;

    height: 200px;
    width: 20px;
    outline: none;
    -webkit-appearance: slider-vertical; /* WebKit */
}

.ZStackScroller-header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}
  
.ZStackScroller-link {
    color: #61dafb;
}
  
@keyframes ZStackScroller-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
}
  